import React from 'react'
import { Link } from 'react-router-dom'

import Slick from 'react-slick';
import './slider.css'

const SliderTemplates = (props) => {

    console.log(props);
    let template = null;

    const settings = {
        dots:true,
        infinite: true,
        arrows: false,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll:1
        // ...props.settings
    }

    switch(props.type){
        case ('featured'):
            template = props.data.map((item, i)=>{
                return(
                    <div key={i}>
                        {item}
                    </div>
                )
            })
            break;
        default:
            template = null;

    }

    return(
        <Slick {...settings} >
            {template}
        </Slick>
    )
}

export default SliderTemplates;


